
    
 @media screen and (min-width: 1px)
{


    .header{
        display:none;
        
    }
    .body{
        width: 100%;
        height: 90em;
        display: flex;
        flex-direction: row;
      }
        
     .body1{
         display: none;
        width: 20%;
      }
    .body2 {
        display: none;
        width: 20%;
      }
    .body3 {
    width: 100%;
      }

    .siguiente{
      display: block;
        background-color: aqua;
        text-align: center;
        
    }  
    } 
     
@media screen and (min-width: 550px)
    
{
    .header{
        display:block;
    }
    .body{
        width: 100%;
        height: 90em;
        display: flex;
        flex-direction: row;
      }
        
     .body1{
        width: 20%;
        display: block;
      }
    .body2 {
        display: block;
        width: 20%;
      }
    .body3 {
    width: 60%;
      }
      .siguiente{
        display: none;
      }
}   